footer {
    background-color: #ffffff;
    color: #a6a8aa;
    position: absolute;
    margin-top: 200px;
    display: flex;
    padding: 20px;
    font-size: 12px;
    justify-content: center;
    word-spacing: 12px;
    list-style: 20px;
    width: 100vw;
    bottom: 0;
}

#end>a:active {
    color: black;
    background-color: transparent;
    text-decoration: underlines;
}

#end>a:hover {
    color: grey;
    background-color: transparent;
    text-decoration: underlines;
}

#end>a:visited {
    color: black;
    background-color: transparent;
    text-decoration: none;
}

#end>a:link {
    color: black;
    background-color: transparent;
    text-decoration: underlines;
}
@media screen and (max-width:900px) {
    footer {
        position: absolute;
        width: 100vw;
        padding: 20px;
    }
}

@media screen and (max-height:680px) {
    footer {
        top: 100px;
        position: relative;
    }
}