* {
    margin: 0;
    padding: 0;
    font-family: Helvetica, Arial, sans-serif;
    box-sizing: border-box;
}

body {
    background-color: whitesmoke;
    position: relative;
    height: 100vh;
    width: 100vw;
    overflow-x: hidden;
}

#container {
    display: flex;
}

#recent {
    display: flex;
    flex-direction: column;
    position: relative;
    left: 35vh;
    top: 15vh;
    margin-right: -20px;
}

#facebook {
    height: 100px;
    width: 210px;
    margin: -20px;
}

#recent>#title {
    font-size: 24.5px;
    font-weight: normal;
    line-height: 32px;
    margin-top: 15px;
    margin-bottom: 10px;
}

#recent>#subtitle {
    color: #606770;
    font-size: 15px;
    line-height: 15px;
    font-weight: 500;
    margin-bottom: 20px;
}

#cards {
    display: flex;
}

#card-1,
#card-2 {
    position: relative;
    background-color: whitesmoke;
    margin-right: 15px;
    border-radius: 5px;
    margin-right: 30px;
}

#card-1:hover,
#card-2:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

#card-1-img,
#card-2-img {
    height: 183px;
    width: 142px;
    border-top-left-radius: 5pt;
    border-top-right-radius: 5pt;
}

#add-account {
    color: #166FE5;
    font-size: 20px;
    line-height: 22px;
    padding: 10px;
    text-align: center;
}

.username {
    font-size: 20px;
    line-height: 22px;
    padding: 10px;
    text-align: center;
    color: rgb(75, 79, 86);
}

#cancel {
    position: absolute;
    left: 8px;
    color: #ffffff;
    top: 5px;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    text-align: center;
    background-color: rgb(63, 62, 62);
    opacity: 0.7;
}

#card-1:hover #cancel {
    background-color: white;
    color: rgb(194, 193, 193);
    height: 25px;
    width: 25x;
    top: -5px;
    left: -5px;
    font-size: 25px;
    transition: 0.3s;
    line-height: 23px;
    cursor: pointer;
    opacity: 1.0;
}

#login {
    position: relative;
    background-color: white;
    width: 410px;
    height: 350px;
    display: flex;
    flex-direction: column;
    top: 17vh;
    justify-content: flex-start;
    border-radius: 10px;
    box-shadow: 0 5px 5px 3px gainsboro;
    padding: 10px 10px 20px 10px;
    align-items: center;
    left: 66vh;
}

#login>a {
    text-decoration: none;
    margin: 20px 0;
    font-size: 15px;
    color: #1877F2;
}

#login>a:hover {
    text-decoration: underline;
    cursor: pointer;
}

hr {
    width: 95%;
    height: 0.1%;
    background-color: rgb(194, 193, 193);
    border: none;
}

#login>input[type=email],
#login>input[type=password] {
    width: 90%;
    padding-left: 10px;
    border-radius: 5px;
    margin: 8px 0;
    font-size: 17px;
    border: 1.5px solid #EEEFF0;
    height: 40%;
}

#login>input[type=password] {
    margin-bottom: 12px;
}

#login>input[type=email]:focus,
#login>input[type=password]:focus {
    outline: none;
    border: 1px solid blue;
    box-shadow: 0 0 5px #719ece;
    caret-color: blue;
}

#login-btn {
    background-color: #1877F2;
    color: #ffffff;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    height: 116px;
    font-weight: bold;
    width: 90%;
}

#login-btn:hover {
    background-color: #166FE5;
    cursor: pointer;
}

#create-new-account {
    margin-top: 20px;
    background-color: #42b72a;
    color: #ffffff;
    font-size: 17px;
    border-radius: 5px;
    border: none;
    height: 116px;
    font-weight: bold;
    width: 56%;
}

#create-new-account:hover {
    background-color: #36a420;
    cursor: pointer;
}

p {
    font-size: 15px;
    position: absolute;
    bottom: -55px;
}
@media screen and (max-width: 1115px) {
    #recent {
        left: 80px;
        margin-left: 25vw;
    }
    #login {
        right: 50px;
    }
}

@media screen and (max-width:900px) {
    body {
        height: 150vh;
    }
    #container {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    #recent {
        align-items: center;
        top: 20px;
        left: 0;
        margin-left: 0%;
    }
    #card-1 {
        margin-right: 5px;
    }
    #card-2 {
        margin-left: 5px;
        margin-right: 0%;
    }
    #login {
        text-align: center;
        top: 80px;
        left: 0;
        margin-right: 0;
        margin-top: -30px;
    }
    #recent,
    #login {
        position: relative;
    }
   
}